body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root { 
  --primary-color: #0A3142; 
  --secondary-color: #59AD55;
  --extra-color-1: #81F299;
  --extra-color-2: #50A68C;

  --primary-color-dark: #051922;
  --secondary-color-dark: #42803f; 
  --extra-color-1-dark: #58a569;
  --extra-color-2-dark: #397765;

  --primary-color-light: #115572;
  --secondary-color-light: #71dd6b; 
  --extra-color-1-light: #9df8b1;
  --extra-color-2-light: #67ddba;
}

.dropdown-item:active {
  background-color: var(--extra-color-2) !important;
}

.btn {
  transition: all 0.3s ease;
}

.btn-primary {
  color: white;
  background-color: var(--primary-color) !important;
  border-color: var(--primary-color) !important;
}

.btn-secondary {
  color: white;
  background-color: var(--secondary-color) !important;
  border-color: var(--secondary-color) !important;
}

.btn:hover {
  color: white;
  background-color: var(--extra-color-1) !important;
  border-color: var(--extra-color-1) !important;
}

.btn:active {
  color: white;
  background-color: var(--extra-color-2) !important;
  border-color: var(--extra-color-2) !important;
}

.btn-primary:disabled {
  background-color: var(--secondary-color) !important;
  border-color: var(--secondary-color) !important;
}

.btn-secondary:disabled {
  background-color: var(--extra-color-1) !important;
  border-color: var(--extra-color-1) !important;
}